.floating-checklist-button{
    border-radius: 100%;
    bottom: 20px;
    right: 20px;
    position: fixed;

    font-size: 18pt;
}

.floating-checklist-button .incomplete-checklist-items-count {
    top: 0px;
    right: 0px;
    position: absolute;

    background-color: red;
    color: white;
    border-radius: 100%;

    width: 21px;
    height: 21px;

    font-size: 10pt;
}


.right-checklist-pane {
    z-index: 9999;
    width: 300px;
    height: 100%;
    right: 0px;
    top: 0px;
    position: fixed;
    border-radius: 15px 0px 0px 15px;
    border: rgb(80, 80, 80) 1px solid;
    background-color: white;
}

.right-checklist-pane .top-area {
    color: white;
    border-radius: 10px 0px 0px 0px;
    border-bottom: 2px solid white;
    background-color: #1c79d0;
}

.right-checklist-pane .top-area h4 {
    font-weight: normal;
}

.right-checklist-pane .body-area{
    padding-top: 10px;
    overflow: scroll;
    height: 80%;
}

.right-checklist-pane .body-area ul {
    color: #002e60;
    list-style: none;
    padding-left: 20px;
}

.right-checklist-pane .body-area ul li {
    margin-top: 10px;
}

.right-checklist-pane .body-area ul li img {
    margin-right: 5px;
}

.right-checklist-pane .body-area ul li:nth-last-child(1) {
    margin-bottom: 10px;
}

.right-checklist-pane .body-area ul li.read {
}

.right-checklist-pane .body-area ul li.completed {
    color: rgb(49, 49, 49);
    text-decoration: line-through;
    text-decoration-color: rgb(49, 49, 49);
}

.right-checklist-pane .body-area ul li span {
    cursor: pointer;
}